.wrapper {
    width: 100%;
    height: 95vh;
    padding: 40px 0;
    box-sizing: border-box;
    background-color: var(--bg-about);
}

.about_wrapper {
    width: 100%;
    height: 92%;
    background-color: var(--bg-about);
}

.about_section {
    width: 85%;
    margin: 0 auto;
    height: 100%;
    background-color: var(--bg-about);
}

.about_section_heading {
    font-size: 2.3em;
    font-weight: 500;
    letter-spacing: 1px;
    padding: 5px 0;
    background-color: var(--bg-about);
    color: var(--text-secondary-1);
}

.blank_wrapper {
    width: 100%;
    padding: 18px;
    margin: 0 auto;
    background-color: var(--bg-about);
}

.about_main_section {
    width: 100%;
    height: 84%;
    display: flex;
    justify-content: space-between;
    background-color: var(--bg-about);
}

.about_bio {
    width: 52%;
    background-color: var(--bg-about);
}

.about_bio p {
    font-size: 1.7em;
    background-color: var(--bg-about);
    color: var(--text-secondary-2);
}

.blank_para {
    padding: 15px;
}

.about_bio_interests_section {
    margin-top: 1.5em;
}

.interests_list {
    background-color: var(--bg-about);
}

.interest_list_item {
    font-size: 1.7em;
    margin-right: 1em;
    text-align: center;
    display: inline-block;
    background-color: var(--bg-about);
    color: var(--text-secondary-2);
}

.about_display_img {
    background-color: var(--bg-about);
    width: 46%;
    position: relative;

}

.display_image {
    image-rendering: auto;
    width: 80%;
    height: 80%;
    position: absolute;
    right: 0;
    border-radius: 30px;

}

@media(max-width:1440px) and (min-width:769px) {
    .about_section_heading {
        font-size: 2em;
        font-weight: 500;
        letter-spacing: 0.5px;
        padding: 3px 0;
    }

    .about_bio p {
        font-size: 1.5em;
    }

    .interest_list_item {
        font-size: 1.5em;
        margin-right: 0.8em;
        text-align: center;
        display: inline-block;
    }

    .about_display_img {
        background-color: var(--bg-about);
        width: 46%;
        position: relative;
    }

    .display_image {
        image-rendering: auto;
        width: 60%;
        height: 55%;
        position: absolute;
        top: 38%;
        left: 50%;
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
    }
}

@media(max-width:768px) and (min-width:600px) {
    .wrapper {
        height: 100vh;
    }

    .about_section_heading {
        font-size: 1.7em;
        font-weight: 500;
        letter-spacing: 1px;
        padding: 5px 0;
        text-align: center;
    }

    .about_main_section {
        display: flex;
        flex-direction: column-reverse;
        text-align: center;
    }

    .about_bio {
        width: 85%;
        margin: 0 auto;
    }

    .about_bio p {
        font-size: 1.4em;
    }

    .interest_list_item {
        font-size: 1.4em;
        margin-right: 1em;
        text-align: center;
        display: inline-block;
    }

    .about_display_img {
        background-color: var(--bg-about);
        width: 46%;
        margin: 0 auto;
        position: relative;
        height: 50%;
    }

    .display_image {
        image-rendering: auto;
        width: 100%;
    }
}


@media(max-width:600px) {
    .wrapper {
        height: 100vh;
    }

    .about_section_heading {
        font-size: 1.4em;
        font-weight: 500;
        letter-spacing: 0.5px;
        padding: 4px 0;
        text-align: center;
    }

    .about_main_section {
        display: flex;
        flex-direction: column-reverse;
        text-align: center;
    }

    .about_bio {
        width: 90%;
        margin: 0 auto;
    }

    .about_bio p {
        font-size: 1.2em;
        font-weight: 400;
    }

    .interest_list_item {
        font-size: 1.1em;
        margin-right: 1em;
        text-align: center;
        display: inline-block;
    }

    .about_display_img {
        background-color: var(--bg-about);
        width: 50%;
        margin: 0 auto;
        position: relative;
        height: 56%;
    }

    .display_image {
        image-rendering: auto;
        width: 100%;
    }
}